/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type='number'] {
  -moz-appearance: textfield;
}

.input_eth::after {
  position: absolute;
  content: 'ETH';
  height: 100%;
  display: grid;
  place-items: center;
  top: 0;
  bottom: 0;
  right: 1rem;
  color: #515151;
}
.input_wks::after {
  position: absolute;
  content: 'WKS';
  height: 100%;
  display: grid;
  place-items: center;
  top: 0;
  bottom: 0;
  right: 1rem;
  color: #515151;
}
